// Funo para calcular o bnus de tempo baseado na pontuao
export const calculateTimeBonus = (score: number): number => {
  // Pontuao mxima possvel  3000 (clique direto no bairro)
  const maxScore = 3000;
  const maxBonus = 4.0; // Mximo de 4 segundos de bnus (mais fcil)
  
  // Calcula o bnus proporcional  pontuao
  // Quanto maior a pontuao, maior o bnus
  // Pontuao mnima para receber bnus  500
  if (score < 500) return 0;
  
  // Normaliza a pontuao entre 0 e 1, considerando 500 como mnimo
  const normalizedScore = (score - 500) / (maxScore - 500);
  
  // Calcula o bnus proporcional
  return Math.min(maxBonus, normalizedScore * maxBonus);
};

export const getProgressBarColor = (timeLeft: number, roundInitialTime: number): string => {
  const percentage = (timeLeft / roundInitialTime) * 100;
  if (percentage > 60) return '#00FF66';
  if (percentage > 30) return '#FFD700';
  return '#FF4444';
};

export const getFeedbackMessage = (distance: number): string => {
  const distanceKm = distance / 1000;
  
  if (distanceKm < 0.5) {
    return "";
  } else if (distanceKm < 1) {
    return "";
  } else if (distanceKm < 2) {
    return "";
  } else if (distanceKm < 5) {
    return "";
  } else {
    return "";
  }
};

